// app.scss

// Variables
@import "variables";

body {
  background-color: $body-bg;
  margin: 0;
  padding: 0;
}

.navBar {
  background-color: $navBarColor;
  .content {
    padding: 0px 30px;
    display: flex;
    align-content: center;
    justify-content: space-between;
    border-radius: 4px;
    box-shadow: 0px 2px 1px -1px rgba(0, 0, 0, 0.2),
      0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 1px 3px 0px rgba(0, 0, 0, 0.12);
  }
  .leftSide {
    display: flex;
    .menu {
      display: none;
      align-content: center;
      margin-right: 20px;
      .sideBar {
        display: none;
      }
    }
    .logo {
      cursor: pointer;
      text-align: center;
      display: flex;
      width: 100%;
      align-content: center;
      color: $fontNavColor;
      padding-left: 30px;
    }
  }

  .rightSide {
    display: flex;

    .navLinks {
      display: flex;
      div {
        display: flex;
        align-items: center;
        font-size: 1.5rem;
        cursor: pointer;
        padding: 1rem;
        color: $fontNavColor;
      }
    }
  }
}

@media screen and (max-width: 950px) {
  .navBar {
    display: block;
    .content {
      padding: 0px 30px;
      display: block;
    }
    .leftSide {
      display: flex;
      .logo {
        text-align: center;
        display: flex;
        justify-content: center;
        padding-left: 0px;
      }
    }
    .rightSide {
      display: flex;
      justify-content: center;
    }
  }
}

@media screen and (max-width: 650px) {
  .navBar {
    display: flex;
    .content {
      padding: 30px;
      display: flex;
      width: 100%;
    }
    .leftSide {
      display: flex;
      width: 100%;
      .menu {
        img {
          position: relative;
          z-index: 100;
        }
        display: flex;
        align-content: center;
        margin-right: 20px;
        .sideBar {
          background-color: $sideBarColor;
          opacity: 0.9;
          height: 100%;
          width: 240px;
          position: absolute;
          z-index: 50;
          top: 0px;
          left: 0px;
          .sideBarList {
            display: flex;
            flex-direction: column;
            align-items: center;
            margin-top: 140px;
            div {
              color: $sideBarFontColor;
              font-size: 1.5rem;
              padding: 28px;
              cursor: pointer;
            }
          }
        }
        .active {
          display: block;
        }
      }
      .logo {
        width: fit-content;
        text-align: center;
        display: flex;
        justify-content: left;
        transition: all 300ms;
        padding-left: 25px;
      }
      .toggled {
        transform: translateX(60%) scale(0.7);
      }
    }
    .rightSide {
      display: none;
    }
  }
}

@media screen and (max-width: 415px) {
  .navBar {
    .leftSide {
      .logo {
        transition: all 300ms;
      }
      .toggled {
        transform: translateX(45%) scale(0.5);
      }
    }
  }
}

.container {
  display: flex;
  .menuContainer {
    .subCategoryMenuContainer {
      position: absolute;
      z-index: 10;
      left: 100%;
      height: 100%;
      width: 100%;
    }
    .sections {
      .categorySection {
        .categoryHeader {
          font-weight: bold;
          padding: 5px 10px;
        }
      }
    }
  }
}

.paper {
  background-color: $categoriesCardBgColor;
  border-radius: 4px;
  box-shadow: 0px 2px 1px -1px rgba(0, 0, 0, 0.2),
    0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 1px 3px 0px rgba(0, 0, 0, 0.12);
}
